  flex-shrink: 0;
  margin-top: clamp(10px, 1.667vw, 20px); }
  .accordion-button::before {
    width: 40%;
    height: 2px;
    content: '';
    position: absolute;
    background-color: #fff;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%); }
  .accordion-button::after {
    height: 40%;
    width: 2px;
    content: '';
    position: absolute;
    background-color: #fff;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    transition: all .4s; }
  .accordion-button.active {
    transform: rotate(180deg); }
    .accordion-button.active::after {
      opacity: 0; }

